let z;
async function load(event) {
  if (event.data.auth) {
    let get_z_options = function() {
      return {
        userID: event.data.id,
        server: "https://sync.hiveos.app/",
        schema,
        kvStore: "idb"
        // ... other options
      };
    };
    z = new Z(get_z_options());
  } else {
    console.log("No auth, no z");
    z = void 0;
  }
  return {
    auth: event.data.auth,
    id: event.data.id,
    groupId: event.data.groupId,
    z
  };
}
export {
  load
};
