import { parse } from "cookie";
import jwt from "jsonwebtoken";
import { d as db, a as users, u as userGroupMembers } from "./schema.js";
import { eq } from "drizzle-orm";
const { verify } = jwt;
async function handle({
  event,
  resolve
}) {
  const { headers } = event.request;
  const cookies = parse(headers.get("cookie") ?? "");
  if (cookies.session) {
    const token = cookies.session;
    try {
      const jwtUser = verify(token, "437baa87d7b1834b2b201dc242d60dace1fe4fa46a4d9483771f6a1492f200b412fbb27297e31ab07be1ea5be8fcfada87a8237408ebc7af13e257402c0afe36");
      if (typeof jwtUser === "string") {
        throw new Error("Something went wrong");
      }
      const account = await db.select().from(users).where(eq(users.email, jwtUser.user)).execute();
      if (!account[0]) {
        throw new Error("User not found");
      }
      const group = await db.select().from(userGroupMembers).where(eq(userGroupMembers.userId, account[0].id)).execute();
      console.log("run group query");
      let groupId = group[0]?.userGroupId;
      if (!groupId) {
        groupId = "0";
      }
      const sessionUser = {
        id: account[0].id,
        email: account[0].email,
        groupId
      };
      event.locals.user = sessionUser;
    } catch (error) {
      console.error(error);
    }
  } else {
    event.locals.user = null;
  }
  return await resolve(event);
}
async function getSession({ locals }) {
  return {
    user: locals.user
  };
}
export {
  getSession,
  handle
};
