import { r as redirect } from "../../../../chunks/index.js";
import { h as hash } from "../../../../chunks/hash.js";
import { d as db, a as users } from "../../../../chunks/schema.js";
import { eq } from "drizzle-orm";
import jwt from "jsonwebtoken";
const { sign } = jwt;
async function load({ locals }) {
  if (locals?.user) {
    throw redirect(302, "/");
  }
  return {
    props: {
      user: locals.user
    }
  };
}
const actions = {
  login: async ({ cookies, request }) => {
    const data = await request.formData();
    const email = data.get("email");
    if (!email) {
      throw redirect(302, "/account/login");
    }
    const password = data.get("password");
    let token;
    const account = await db.select().from(users).where(eq(users.email, email)).execute();
    const hashedPassword = hash(password);
    if (account[0] && account[0].password === hashedPassword) {
      token = sign(
        {
          user: email
        },
        "437baa87d7b1834b2b201dc242d60dace1fe4fa46a4d9483771f6a1492f200b412fbb27297e31ab07be1ea5be8fcfada87a8237408ebc7af13e257402c0afe36",
        { expiresIn: "30d" }
      );
    } else {
      console.log("wrong password");
    }
    if (token) {
      cookies.set("session", token, {
        path: "/",
        httpOnly: true,
        sameSite: "strict",
        secure: false,
        maxAge: 60 * 60 * 24 * 30
        // 30 days
      });
    }
    throw redirect(302, "/");
  }
};
export {
  actions,
  load
};
